
.buttonAction {
    background-color: transparent;
    height: 20px;
    weigth: 20px;
    border: none;
}

.table {
    width: 100%;
    text-align: start;
    border-collapse: collapse;
    white-space: normal;
    word-break: break-all;
    margin-bottom: 50px;
}

th, td {
    text-align: start;
    height: 30px;
    word-wrap: break-word;
}

td:nth-child(1) {
    word-wrap: break-word;
    box-sizing: border-box;
    padding: 0 30px 0 36px;
}

.thName {
    padding-left: 36px;
    width: 280px;
}

.trHeader {
    background-color: #EFEFEF;
}

.thCards {
    width: 130px;
}

.thUpdated {
    width: 216px;
}

.thCrated {
    width: 125px;
}

.thActions {
    width: 300px;
}

@media screen and (min-width: 0px) and (max-width: 767px) {

    table thead {
        display: none;
        width: 100%;
    }


    table tr {
        display: block;
        border-bottom: none;
        margin-bottom: 30px;
    }

    table td {
        width: 100%;
        height: 30px;
        display: block;
        text-align: right;
        font-size: 20px;
        border-bottom: 1px dotted #ccc;
        border-right: 1px solid transparent;
        overflow: hidden;
    }

    table td:first-child {
        padding: 0;
        margin: 0;
    }

    table td:last-child {
        border-bottom: 0;
    }

    .thName {
        border-bottom: 0;
        padding: 0;
        margin: 0;
    }

    table td:before {
        content: attr(data-label);
        float: left;
        text-transform: inherit;
        font-weight: bold;
    }

}
