.subtitle {
    width: 100%;
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
}

.inputRange {
    display: flex;
}

.inputContainer {
    display: block;
    width: 450px;
    margin-right: 100px;
}

.spanBefore {
    margin-right: 20px;
    border: 2px solid #D9D9D9;
    font-size: 16px;
    font-weight: 600;
    padding: 0 13px;
    line-height: 32px;

}

.spanAfter {
    margin-left: 20px;
    border: 2px solid #D9D9D9;
    font-size: 16px;
    font-weight: 600;
    padding: 0 10px;
    line-height: 36px;
}

@media screen and (min-width: 0px) and (max-width: 1000px) {
    .subtitle {
        display: flex;
        flex-direction: column-reverse;
        align-items: start;
        justify-content: revert;
        margin-bottom: 0;
    }

    .inputContainer {
        margin-bottom: 30px;
    }

    .inputRange {
        margin-bottom: 30px;
    }

    .inputContainer {
        width: 100%;
    }

    .subtitle {
        display: flex;
        width: 100%;
    }

}

@media screen and (min-width: 768px) and (max-width: 1440px) {

    .inputContainer {
        width: 350px;
        margin-right: 50px;
    }

    .inputContainer {
        display: block;
        width: 350px;
        margin-right: 10px;
    }

}