.wrapButtons {
    display: flex;
    flex-direction: column;
    margin-right: 50px;
}

.buttonLabel {
    margin-bottom: 10px;
    font-weight: 500;
    color: #000000;
    font-size: 14px;
}

.buttonsContainer {
    display: flex;
    margin-right: 10px;
}

.button {
    height: 30px;
    padding: 5px;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
}


@media screen and (min-width: 769px) and (max-width: 1440px) {
    .wrapButtons {
        margin-right: 10px;
    }

}