.headerWrap {
    width: 100%;
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 12px 100px;
    box-sizing: border-box;

}

.hr {
    height: 3px;
    margin: 0;
    padding: 0;
    background-color: #F9F9FA;
    box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.30) inset;
}

.headerWrap:hover {
    opacity: 0.5;
}

.userName {
    margin-right: 30px;
    font-weight: 500;
    font-size: 20px;
}


@media screen and (min-width: 0px) and (max-width: 767px) {
    .headerWrap {
        padding: 20px;
    }

}

@media screen and (min-width: 768px) and (max-width: 1440px) {
    .headerWrap {
        padding: 12px 50px;
    }

}
