table {
    width: 100%;
    text-align: start;
    word-break: break-all;
    margin-bottom: 50px;
    border-collapse: collapse;
    text-overflow: ellipsis;
}

.buttonAction {
    background-color: #fff;
    border: none;

}

.tdAction {
    white-space: nowrap;
}

thead {
    white-space: nowrap;
}

th, td {
    text-align: start;
    height: 30px;
}

td:nth-child(1) {
    padding-left: 36px;
}

.thName {
    padding-left: 36px;
    width: 280px;
}

thead {
    background-color: #EFEFEF;
}

.thAnswer {
    width: 280px;
}

.thUpdated {
    width: 150px;
}

.thCrated {
    width: 125px;
}

@media screen and (min-width: 0px) and (max-width: 767px) {

    table thead {
        display: none;
        width: 100%;
    }


    table tr {
        display: block;
        border-bottom: none;
        margin-bottom: 30px;
    }

    table td {
        width: 100%;
        height: 30px;
        display: block;
        text-align: right;
        font-size: 20px;
        border-bottom: 1px dotted #ccc;
        border-right: 1px solid transparent;
        overflow: hidden;
    }

    table td:first-child {
        /*border-bottom: 0;*/
        padding: 0;
        margin: 0;
    }

    table td:last-child {
        border-bottom: 0;
    }

    .thName {
        border-bottom: 0;
        padding: 0;
        margin: 0;
    }

    table td:before {
        content: attr(data-label);
        float: left;
        text-transform: inherit;
        font-weight: bold;
    }

}

