
.buttonAction {
    background-color: transparent;
    height: 20px;
    width: 20px;
    border: none;
}

.addButton {
    width: 184px;
    height: 36px;
    border-radius: 30px;
    background-color: #366EFF;
    border: none;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
}

button:hover {
    opacity: 0.7;
}

button:disabled {
    opacity: 0.5;
}

.aboutButton {
    border: none;
    background-color: inherit;
    display: flex;
    align-items: center;
}

.headerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modalQuestion {
    position: fixed;
    top: calc(50vh - (50vh / 2));
    left: calc(50vw - (50vh / 2));
    background: white;
    z-index: 21;
    display: block;
    box-sizing: border-box;
    padding: 20px;
    width: 395px;
    height: 180px;
}

.paragraphQuestionModal {
    margin-bottom: 30px;
}

.modal {
    position: fixed;
    top: calc(50vh - (50vh / 2));
    left: calc(50vw - (50vh / 2));
    background: white;
    z-index: 21;
    display: block;
    box-sizing: border-box;
    padding: 20px;
    width: 395px;
    height: 250px;
}

.editableSpanModal {
    font-size: 16px;
    color: #000000;
    border: none;
    border-bottom: 2px solid rgba(217, 217, 217, 1);
    margin-bottom: 30px;
    outline: none;
}

.inputSubtitle {
    display: block;
    height: 25px;
    border: 1px solid #D9D9D9;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #000000;
    margin-top: 10px;
    background-image: url("../../icons/glass.svg");
    background-repeat: no-repeat;
    background-position: left;
    background-size: 25px 25px;
    padding-left: 25px;
}

.hrModal {
    width: 100%;
    height: 2px;
    background-color: rgba(217, 217, 217, 1);
    position: relative;
    margin-bottom: 30px;
}

.inputContainer {
    margin-bottom: 20px;
    width: 500px;
}

@media screen and (min-width: 0px) and (max-width: 767px) {

    .headerContainer {
        flex-direction: column;
        align-items: start;
    }

    .addButton {
        margin-bottom: 30px;
    }
    .inputContainer {
        width: 100%;
    }
}

